import styled from '@emotion/styled';
// Local
import { useTab } from '../Hooks/useTab';
import { TabTypes } from '../Types';

/**
 * 탭 아이템 컴포넌트를 나타냅니다.
 * @param tabId - 탭의 ID입니다.
 * @param children - 탭 아이템의 내용입니다.
 * @param rest - 컴포넌트에 전달할 추가 프롭스입니다.
 * @returns 탭 아이템을 나타내는 리액트 컴포넌트입니다.
 */
export const Item: TabTypes['Parts']['Item'] = ({ tabId, children, ...rest }) => {
  const { changeTab } = useTab()

  const onChangeTab = (e: React.MouseEvent<HTMLDivElement, MouseEvent>) => {
    e.preventDefault()
    changeTab({ tabId })
  }

  return (
    <StyledItem {...rest} tabId={tabId} onClick={onChangeTab}>
      {children}
    </StyledItem>
  )
}

const StyledItem = styled.div<TabTypes['Props']['Item']>`
  padding          : 16px;
  color: black;
  background-color : ${({ b }) => b ?? '#fff1f1'};
  border-top       : 1px solid #dee2e6;
  display          : flex;
  justify-content  : flex-end;
`